.center{
  text-align: center;
}
nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: rgb(59, 104, 201);
}
nav li {
  display: inline;
  float: left;
  

}
nav a {
  display: block;
  color: rgb(0, 0, 0);
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}
nav a:hover {
  background-color: #111;
}
li:last-child {
  border-right: none;
}
.homeColumn1 {
  float: left;
  width: 25%;    
  font-family: 'Syne Tactile', cursive;
}

.homeColumn2{
  float: right;
  width: 25%;   
  font-family: 'Syne Tactile', cursive;
}